<template>
  <section class="crowdfunding-section">
    <div class="container">
      <h1 class="main-title">CROWDFUNDING</h1>
      
      <div class="white-card">
        <h2 class="section-title">Fund Allocation</h2>
        <a class="partnership" href="https://www.cls.global/" target="_blank" rel="noopener">
          <img src="https://solanabitcat.com/data/cls.png" alt="CLS" class="logo no-invert" />
        </a>
        
        <p class="description">
          The BITCAT CTO team has <strong>completed a <a href="https://x.com/CoinLiquidity/status/1869046873459220817" target="_blank" rel="noopener">contract<i class="fas fa-external-link-alt external-icon"></i></a> with <a href="https://www.cls.global/" target="_blank" rel="noopener">the CLS GLOBAL<i class="fas fa-external-link-alt external-icon"></i></a></strong> and is
          currently raising funds for liquidity provision through crowdfunding.
        </p>

        <h2 class="section-title">Funding Target</h2>
        
        <div class="target-block">
          <h3>10M worth of $BITCAT</h3>
          <div class="treasury-info">
            <span class="treasury-label">TREASURY</span>
            <span class="amount">{{ bitcatAmount }} BITCAT = ${{ bitcatValue }}</span>
          </div>
          <div class="progress-container">
            <div class="progress-bar" :style="{ width: bitcatProgress + '%' }">
              {{ bitcatProgress }}%
            </div>
          </div>
        </div>

        <div class="target-block">
          <h3>${{ targetAmount }}K USDC + SOL</h3>
          <div class="treasury-info">
            <span class="treasury-label">TREASURY</span>
            <span class="amount">{{ usdcAmount }} USDC + {{ solAmount }} SOL = ${{ usdcSolValue }}</span>
          </div>
          <div class="progress-container">
            <div class="progress-bar" :style="{ width: (usdcSolProgress < 10 ? 10 : usdcSolProgress) + '%' }">
              {{ usdcSolProgress }}%
            </div>
          </div>
        </div>

        <div class="current-price">
            Current BITCAT Price: $<span id="bitcat-price-usdt">{{ bitcatPrice }}</span>
          </div>

        <div class="wallet-section">
          <h3>Multi-Sig Wallet Address (4/10)</h3>
          <div class="wallet-address">
            <span>2iayT6ppVjZczeYPVWSf6HVJTQJNKNainawRiri4xGXC</span>
            <button class="copy-button" @click="copyAddress" :class="{ 'copied': isCopied }">
              <i class="fas fa-copy"></i>
              <span class="tooltip" :class="{ 'show': isCopied }">Copied!</span>
            </button>
          </div>
          <p>Please donate to this address for crowdfunding.</p>
        </div>

        <div class="community-note">
          <span class="icon"><i class="fas fa-users"></i></span>
          <p>
            With small contributions, our strong growing community can achieve this goal together! We are community take over project.
          </p>
        </div>
      </div>
    </div>
  </section>
