<script>
import { ref, onMounted, onBeforeUnmount } from 'vue'

const BITCAT_TOTAL = 10 * 1000 * 1000 // 10M
const USDC_SOL_TOTAL = 30 * 1000 // 30k$

export default {
  name: 'CrowdfundingSection',
  setup() {
    const bitcatAmount = ref(0)
    const bitcatValue = ref(0)
    const bitcatProgress = ref('0')
    const targetAmount = ref(30)
    const bitcatPrice = ref(0)
    const usdcAmount = ref(0)
    const solAmount = ref('0')
    const usdcSolValue = ref('0')
    const usdcSolProgress = ref('0')
    const isCopied = ref(false)
    const updateTimer = ref(null)

    const formatPrice = (price) => {
      const num = parseFloat(price)
      if (num === 0) return '0'
      
      // Find the position of first non-zero digit after decimal
      const str = num.toString()
      const decimalIndex = str.indexOf('.')
      if (decimalIndex === -1) return str
      
      let significantDigits = 0
      for (let i = decimalIndex + 1; i < str.length; i++) {
        if (str[i] !== '0') {
          significantDigits = i - decimalIndex
          break
        }
      }
      
      // Show 3 digits after the first significant digit
      return num.toFixed(significantDigits + 2)
    }

    const setBitcatPrice = (price) => {
      const finalPrice = parseFloat(price)
      const d = parseFloat(price)
      const increment = (finalPrice - d) / 3
      if (d < finalPrice) {
        bitcatPrice.value = formatPrice(d + increment)
      } else {
        bitcatPrice.value = formatPrice(finalPrice)
      }
    }

    const updateProgress = (progress, type) => {
      if (type === 'bitcat') {
        bitcatProgress.value = progress
        localStorage.setItem('progress-bitcat', progress)
      } else {
        usdcSolProgress.value = progress
        localStorage.setItem('progress-sol-usdc', progress)
      }
    }

    const calcBITCAT = (amount) => {
      return ((amount / BITCAT_TOTAL) * 100).toFixed(2)
    }

    const calcUSDCAndSOL = (value) => {
      return ((value / USDC_SOL_TOTAL) * 100).toFixed(2)
    }

    const getDonateData = async () => {
      try {
        const response = await fetch('https://v4-api.squads.so/balancesDAS/2iayT6ppVjZczeYPVWSf6HVJTQJNKNainawRiri4xGXC?sendAll=true&cacheBypass=false&useProd=true')
        const json = await response.json()
        const data = json.balances.reduce((ret, cur) => {
          ret[cur.symbol] = cur
          return ret
        }, {})

        if (data['BITCAT']) {
          const bitcat_progress = calcBITCAT(data['BITCAT'].uiAmount)
          updateProgress(bitcat_progress, 'bitcat')
          bitcatAmount.value = data['BITCAT'].uiAmount.toFixed(2)
          bitcatValue.value = data['BITCAT'].uiPrice.toFixed(2)
          setBitcatPrice(data['BITCAT'].pricePerUnit)
        }

        if (data['USDC'] && data['SOL']) {
          const usdc_sol_value = data['USDC'].uiPrice + data['SOL'].uiPrice
          usdcAmount.value = data['USDC'].uiAmount.toFixed(2)
          solAmount.value = data['SOL'].uiAmount.toFixed(2)
          usdcSolValue.value = usdc_sol_value.toFixed(2)
          const usdc_sol_progress = calcUSDCAndSOL(usdc_sol_value)
          updateProgress(usdc_sol_progress, 'usdc-sol')
        }
      } catch (error) {
        console.error('Error fetching donate data:', error)
      }
    }

    const updateBitcatPrice = (finalPrice) => {
      let currentPrice = 0
      const updateNumber = () => {
        const increment = (finalPrice - currentPrice) / 3
        if (currentPrice < finalPrice) {
          currentPrice += increment
          bitcatPrice.value = formatPrice(currentPrice)
          setTimeout(updateNumber, 100)
        } else {
          bitcatPrice.value = formatPrice(finalPrice)
        }
      }
      updateNumber()
    }

    const startUpdateLoop = () => {
      updateTimer.value = setInterval(getDonateData, 3000)
    }

    const copyToClipboard = (text) => {
      navigator.clipboard.writeText(text).then(() => {
        console.log('Text copied to clipboard')
      }).catch((error) => {
        console.error('Error copying text to clipboard:', error)
      })
    }

    const copyAddress = () => {
      navigator.clipboard.writeText('2iayT6ppVjZczeYPVWSf6HVJTQJNKNainawRiri4xGXC')
        .then(() => {
          isCopied.value = true
          setTimeout(() => {
            isCopied.value = false
          }, 2000)
        })
    }

    onMounted(async () => {
      // Restore saved progress
      bitcatProgress.value = localStorage.getItem('progress-bitcat') || '0'
      usdcSolProgress.value = localStorage.getItem('progress-sol-usdc') || '0'
      
      // Start updates
      getDonateData()
      startUpdateLoop()
    })

    onBeforeUnmount(() => {
      clearInterval(updateTimer.value)
    })

    return {
      bitcatAmount,
      bitcatValue,
      bitcatProgress,
      targetAmount,
      bitcatPrice,
      usdcAmount,
      solAmount,
      usdcSolValue,
      usdcSolProgress,
      copyToClipboard,
      copyAddress,
      isCopied,
    }
  },
}
</script>

